.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  text-align: center;
}

.counter {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.counterBox {
  width: 100%;
  max-width: 500px;
  padding: 30px 50px;
  background-color: 	#fff;
  color: #000;
  text-align: center;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.counterTitle {
  letter-spacing: 1px;
  margin-bottom: 15px;
}

.count {
  margin-bottom: 15px;
  color: #1B2430;
}

button {
  text-transform: capitalize;
  text-align: center;
  font-size: 16px;
  border-radius: 5px;
  padding: 10px 25px;
  cursor: pointer;
  margin: 0 10px;
  transition: all 0.2s;
}

.plus {
  background-color: transparent;
  border: 1px solid green;
  color: green;
}

.plus:hover {
  background-color: green;
  color: #fff;
}

.minus {
  background-color: transparent;
  border: 1px solid red;
  color: red;
}

.minus:hover {
  background-color: red;
  color: #fff;
}



